<template>
  <div style="padding: 20px 20px;width: calc(100% - 40px);height: calc(100% - 100px); background: #f8f8f8; display: flex; flex-direction: column">
    <div class="table-bar">
      <div style="align-items: center;display: flex;padding-left: 10px;position: relative;">
        <el-badge :value="0" size="mini" :hidden="true" class="display-flex" style="align-items: center;">
          <el-button @click="showAdvanceFilters = !showAdvanceFilters">
            <el-icon
                size="20"
            >
              <Filter></Filter>
            </el-icon>
            <span style="font-size: 16px;">Filter</span>
          </el-button>
        </el-badge>
        <transition name="slide-fade">
          <div v-show="showAdvanceFilters" style="position: absolute; height: fit-content; width: 500px;left: 0;background: white;top: 45px;box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;z-index: 999">
            <div style="display: flex;flex-direction: column;">
              <div style="height: 100px;width: 100%;">

              </div>
              <div style="min-height: unset;display: flex;justify-content: flex-end; padding: 10px 20px;">
                <el-button type="primary">Submit</el-button>
                <el-button>Reset</el-button>
              </div>
            </div>
          </div>
        </transition>
      </div>
      <div class="action-button-group">
        <el-button :icon="getPlusIcon" type="primary">Add New</el-button>
<!--        <el-button type="success">Active</el-button>-->
<!--        <el-button type="warning">Inactive</el-button>-->
        <el-button type="danger" @click="deleteFunc">Delete</el-button>
        <el-button type="info" @click="exportFunc">Export</el-button>
      </div>
    </div>
    <div style="height: 100%;width: 100%;margin-top: 20px;padding: 5px;background: white;box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;display: flex;flex-direction: column;">
      <div style="display: flex;justify-content: space-between;margin-bottom: 10px;width: 100%;">
        <el-pagination
            style="height: 40px;position: relative;flex: 1;"
            v-model:current-page="p_pageNo"
            layout="slot, prev, pager, next, jumper"
            :page-sizes="[10, 25, 50, 100]"
            :page-size="p_perPage"
            :total="p_total">
          <div style="position: absolute;font-weight: 400;left: 0;">Showing {{ fromPage }} to {{ toPage }} of {{ p_total }} entries </div>
        </el-pagination>
        <span style="display: flex; align-items: center;">
        <el-input style="margin-left: 10px;width: 350px;" v-model="searchVal" clearable>
          <template #prepend>
            <el-popover
                trigger="click"
                placement="bottom"
                :width="150"
            >
              <div>
                <div class="search-fields" v-for="each in searchColumns" :class="each.value === selectedSearch ? 'is-active' : ''" style="text-align: center;padding: 5px;font-size: 13px;" @click="selectedSearch = each.value">
                  {{ each.name }}
                </div>
              </div>
      <template #reference>
          <el-button
              type="default"
              ref="SearchListBtn"
              size="small"
          >
            <span class="fa-stack text-light">
                  <el-icon :size="12">
                    <Search />
                  </el-icon>
                    <el-icon :size="8" style="margin-left: 5px;">
                      <CaretBottom />
                    </el-icon>
                  </span>
                </el-button>
              </template>
            </el-popover>
        </template>
        </el-input>
      </span>
      </div>
    <el-table :data="filterTableData"
              @filter-change="filterChange"
              :default-sort="{ prop: 'CREATED_DATE', order: 'descending' }"
              border
              @selection-change="handleSelectionChange"
              style="height: 100%;width: calc(100% - 10px);"
              type="selection">
      <el-table-column type="selection" width="55" />
      <el-table-column sortable prop="DRIVER_NAME" label="Driver Name" />
      <el-table-column sortable prop="E_MAIL" label="Email" />
      <el-table-column sortable prop="PH_NO" label="Phone Number" />
      <el-table-column sortable prop="FLEET" label="Fleet" />
      <el-table-column sortable prop="ONLINE" label="Availability" :filter-method="filterHandler" :filters="[
        { text: 'Online', value: true },
        { text: 'Offline', value: false }
      ]">
        <template #default="scope">
          <div class="avail-field" style="display: flex; flex-direction: column;">
            <el-tag style="width: fit-content;font-size: 12px;;" round size="small" :type="scope.row.ONLINE ? 'success' : 'info'" effect="dark"> {{ scope.row.ONLINE ? 'Online' : 'Offline' }} </el-tag>
            <div style="font-size: 13px;margin-top: 3px;">{{ scope.row.LAST_SEEN }}</div>
          </div>
        </template>
      </el-table-column>
      <el-table-column sortable prop="CREATED_DATE" label="Created On" />
      <el-table-column sortable prop="STATUS" label="Status" :filter-method="filterHandler" :filters="[
        { text: 'Active', value: true },
        { text: 'Inactive', value: false }
      ]">
        <template #default="scope">
          <el-tag :type="scope.row.STATUS ? 'success' : 'warning'" effect="dark"> {{ scope.row.STATUS ? 'Active' : 'Inactive' }} </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="STATUS" label="Actions">
        <template #default="scope">
          <el-button size="small"  type="info" :icon="getEditIcon" />
          <el-button size="small" type="primary" :icon="getViewIcon" />
        </template>
      </el-table-column>
    </el-table>
    </div>
  </div>
</template>

<script>
import { ElMessage, ElMessageBox } from 'element-plus'
import { Plus, Edit, View, Search, CaretBottom, Filter } from '@element-plus/icons-vue'
export default {
  name: "ViewDrivers",
  components: { Search, CaretBottom, Filter },
  data(){
    return {
      multiSelectArray: [],
      showAdvanceFilters: false,
      searchColumns: [
        { name: "Driver Name", value: "DRIVER_NAME"},
        { name: "E mail", value: "E_MAIL"},
        { name: "Phone no", value: "PH_NO"},
        { name: "Fleet", value: "FLEET"},
        { name: "Last Seen", value: "LAST_SEEN"},
        { name: "Created Date", value: "CREATED_DATE"}
      ],
      selectedSearch: "DRIVER_NAME",
      searchVal: "",
      p_perPage: 25,
      p_pageNo: 1,
      filterCols: {

      }
    }
  },
  computed: {
    filterTableData() {
      return this.tableData.filter(
          (data) =>
              !this.searchVal ||
              data[this.selectedSearch].toLowerCase().includes(this.searchVal.toLowerCase())
      )
    },
    p_total(){
      return this.tableData.length;
    },
    getPlusIcon(){
      return Plus;
    },
    getEditIcon(){
      return Edit;
    },
    getViewIcon(){
      return View;
    },
    tableData(){
      return [
        {
          DRIVER_NAME: "Jaswanth SP",
          E_MAIL: "jaswanth.sp@gmail.com",
          PH_NO: "6381360525",
          FLEET: "TN012379",
          ONLINE: true,
          LAST_SEEN: "2016-05-01 1:12 am",
          CREATED_DATE: "2016-05-02",
          STATUS: true
        },
        {
          DRIVER_NAME: "Sivakumar",
          E_MAIL: "sivakumar@gmail.com",
          PH_NO: "6381360312",
          FLEET: "TN012379",
          ONLINE: false,
          LAST_SEEN: "2016-05-01 1:12 pm",
          CREATED_DATE: "2016-05-01",
          STATUS: false
        }
      ]
    }
  },
  methods: {
    deleteFunc(){
      if(this.multiSelectArray.length !== 0){
        ElMessageBox.confirm(
            'Are you sure want to delete?',
            'Warning',
            {
              confirmButtonText: 'OK',
              cancelButtonText: 'Cancel',
              type: 'warning',
            }
        ).then(() => {
              ElMessage({
                type: 'success',
                message: 'Delete completed',
              })
            });
      }
      else{
          ElMessage({
            type: 'info',
            message: 'Please select rows to delete',
          })
      }
    },
    exportFunc(){
      ElMessage({
        type: 'success',
        message: 'Exported as CSV',
      })
    },
    handleSelectionChange(selected){
      if(typeof selected === undefined) return;
     this.multiSelectArray = selected;
    },
    filterHandler(value, row, column){
      const property = column['property'];
      return row[property] === value;
    },
    filterChange(filters){
      console.log(filters);
    }
  }
}
</script>

